import React from 'react'

const LinkCard = ({ link }) => (
    <>
        <h2></h2>

        <p> : <a href={link.to} target='_blank' rel='noopener noreferrer'>{link.to}</a></p>
        <p>: <a href={link.from} target='_blank' rel='noopener noreferrer'>{link.from}</a></p>
        <p>   : <strong>{link.clicks}</strong></p>
        <p> : <strong>{new Date(link.date).toLocaleDateString()}</strong></p>
    </>
)

export default LinkCard